// Stripping Styles 

html {
  font-size: 62.5%;
}

h1,
h2,
h3,
h4,
ul,
p,
fieldset,
legend {
  margin: 0;
  padding: 0;
}

.wrapper {
  max-width: 1200px;
  width: 80%;
  margin: 0 auto;
}

ul {
  list-style: none;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  font-family: $bodyFont;
  border: none;
  cursor: pointer;
}

input {
  background: none;
  border: none;
}

fieldset {
  border: none;
}

// General Styles

h1 {
  @include font($headingFont, 6rem);
  text-transform: uppercase;
}

// Colour
h1,
input {
  color: $white;
}

h2,
p,
legend,
input::placeholder {
  color: $black;
}

h2 {
  @include font($bodyFont, 3.5rem);
}

p {
  @include font($bodyFont, 2rem);
}

legend {
  @include font($headingFont, 3.5rem);

  margin-bottom: 10px;
  text-transform: uppercase;
}

input {
  @include font($bodyFont, 2rem);

  width: 100%;
  padding: 10px 20px;

  border-radius: 5px;
  background-color: $primaryColor;
}
